import React, { useState, useEffect } from "react";
import { Modal, Form, Divider, Button, message } from "antd";
import { FormInput, FlexEnd, FormTextArea } from "..";
import { usePostData } from "../../Hooks";
import { setInput } from "../../Utils/myFormat";
import { FiNavigation } from "react-icons/fi";

const ModalRadiusCreate = ({ visible, onCancel, setUpdateData }) => {
  const [title, setTitle] = useState("");
  const [radius, setRadius] = useState("");
  const [remark, setRemark] = useState("");

  const resetState = () => {
    setTitle("");
    setRadius("");
    setRemark("");
    form.resetFields();
  };

  const [form] = Form.useForm();

  const [dataPost, setDataPost] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess, loading } = usePostData(
    dataPost.url,
    dataPost.data,
    dataPost.trigger
  );

  const handlePostRadius = () => {
    if (!title || !radius) {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    const dataPost = {
      title: title,
      radius: radius,
      remark: remark,
    };
    console.log("data post: ", dataPost);

    setDataPost({
      url: "site/radius",
      data: dataPost,
      trigger: Date.now(),
    });
  };

  useEffect(() => {
    if (onSuccess.includes("success")) {
      message.success(`Berhasil Create Radius ${title}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onSuccess]);

  return (
    <Modal
      title="Create Radius"
      visible={visible}
      onCancel={onCancel}
      footer={false}
    >
      <Form form={form} name="createradius" autoComplete="off">
        <FormInput
          label="Title"
          requiredLabel
          name="title"
          required
          message="please input title!"
          placeholder="input title"
          onChange={(e) => setInput(e, setTitle)}
        />
        <FormInput
          label="Radius in Meter"
          requiredLabel
          name="radius"
          required
          message="please input radius!"
          placeholder="input radius"
          onChange={(e) => setInput(e, setRadius)}
          type="number"
        />
        <FormTextArea
          label="Remark"
          name="remark"
          rows={4}
          placeholder="input remark"
          onChange={(e) => setInput(e, setRemark)}
        />
        <Divider />
        <FlexEnd>
          <Form.Item>
            <Button
              type="primary"
              shape="round"
              icon={<FiNavigation style={{ marginRight: "6px" }} />}
              onClick={handlePostRadius}
              loading={loading}
              htmlType="submit"
            >
              Create Radius
            </Button>
          </Form.Item>
        </FlexEnd>
      </Form>
    </Modal>
  );
};

export default ModalRadiusCreate;
