import React, { useState, useMemo, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
  Breadcrumb,
  Typography,
  Row,
  Button,
  Table,
  Skeleton,
  message,
} from "antd";
import withAuth from "../../HOC/withAuth";
import { Card, FlexEnd, GridLarge, GridSmall } from "../../Components";
import { FiPlusSquare } from "react-icons/fi";
import { useGetData, useDisclosure, useUpdateData } from "../../Hooks";
import ModalRadiusCreate from "../../Components/Site/ModalRadiusCreate";
import { myMeterFormat } from "../../Utils/myFormat";
import { gradPurple1, textColor } from "../../Utils/colors";

const { Title, Text } = Typography;

const RadiusConfig = () => {
  const [updateData, setUpdateData] = useState(0);
  const { data, loading } = useGetData("site/list_radius", updateData);
  const { data: currentRadius, loading: loadingCurrentRadius } = useGetData(
    "site/current_radius",
    updateData
  );

  console.log(currentRadius);

  const newRadius = useMemo(() => data, [data]);

  const { isOpen, onOpen, onClose } = useDisclosure();
  const navigate = useNavigate();

  const [dataUpdate, setDataUpdate] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess: successUpdate, loading: loadingUpdate } = useUpdateData(
    dataUpdate.url,
    dataUpdate.data,
    dataUpdate.trigger
  );

  const handleUpdateRadius = (id) => {
    setDataUpdate({
      url: `site/update_current_radius?id=${id}`,
      data: {},
      trigger: Date.now(),
    });
  };

  useEffect(() => {
    if (successUpdate.includes("success")) {
      message.success("Berhasil Update Radius");
      setUpdateData(Date.now());
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [successUpdate]);

  const columns = [
    {
      title: "#",
      dataIndex: "id",
      width: 70,
    },
    {
      title: "Title",
      dataIndex: "title",
    },
    {
      title: "Radius",
      dataIndex: "value",
      render: (text) => myMeterFormat(Number(text)),
    },
    {
      width: 100,
      render: (text, record) => (
        <Button
          shape="round"
          type="primary"
          onClick={() => handleUpdateRadius(record.id)}
          loading={loadingUpdate}
        >
          Select
        </Button>
      ),
    },
  ];

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item
          style={{ cursor: "pointer" }}
          onClick={() => navigate("/site")}
        >
          Site
        </Breadcrumb.Item>
        <Breadcrumb.Item>Radius Config</Breadcrumb.Item>
      </Breadcrumb>
      <ModalRadiusCreate
        visible={isOpen}
        onCancel={onClose}
        setUpdateData={setUpdateData}
      />
      <div>
        <Card>
          <Title
            level={3}
            type="secondary"
            style={{ marginRight: 20, marginBottom: 30 }}
          >
            Radius Config
          </Title>
          <Row gutter={[16, 10]}>
            <GridLarge>
              <Button
                shape="round"
                icon={<FiPlusSquare style={{ marginRight: 6 }} />}
                onClick={onOpen}
              >
                New Radius
              </Button>
            </GridLarge>
            <GridSmall>
              <Skeleton loading={loadingCurrentRadius}>
                <FlexEnd>
                  <Text style={{ fontSize: 18, color: textColor }}>
                    Current radius is{" "}
                    <Text strong style={{ color: gradPurple1, fontSize: 20 }}>
                      {myMeterFormat(Number(currentRadius))}
                    </Text>
                  </Text>
                </FlexEnd>
              </Skeleton>
            </GridSmall>
          </Row>
          <div style={{ marginTop: "20px" }}>
            <Table
              columns={columns}
              dataSource={newRadius}
              rowKey={(record) => record.id}
              loading={loading}
              scroll={{ y: 500 }}
            />
          </div>
        </Card>
      </div>
    </div>
  );
};

export default withAuth(RadiusConfig, [1]);
